<template>
  <div id="app">
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg-3 col-sm-12 today">
          <div v-if="search">
            <p class="searchBar" @click="search = !search">Search For Places</p>
            <i class="fas fa-map-marker-alt gps" @click="getlocate"></i>
            <br />

            <img class="cloud" :src="cloud" alt="gg" />

            <img class="todayImage" :src="clear" />
            <br />

            <div class="todayDetails">
              <p class="todayDeg">
                {{ weather.temp
                }}<span class="deg" v-if="iscelsius">&#176;C</span
                ><span class="deg" v-else>&#176;F</span>
              </p>
              <p class="detail">{{ weather.description }}</p>
              <div class="footerToday">
                <p v-if="query">
                  <span class="space"> Today &middot; </span
                  >{{ weather.weekday }}, {{ weather.day }} {{ weather.month }}
                </p>
                <!-- <p>{{weather.unixtime}}</p> -->
                <p class="city">
                  <i class="fas fa-map-marker-alt"></i>
                  <span>{{ weather.city }}</span>
                </p>
              </div>
            </div>
          </div>

          <div v-else class="searchDiv">
            <vue-suggest
              v-model="query"
              :list="cityarr"
              :filter-by-query="true"
              placeholder="search location"
            >
            </vue-suggest>

            <button type="button" class="btn" @click="fetchData">Search</button>
            <br />
          </div>
        </div>
        <div class="col-lg-9 col-sm-12 otherDays">
          <br>
          <span class="letter1" @click="Celsius">&#176;C</span>
          <span class="two" @click="Fahrenheit">&#176;F </span>
          <br />

          <div class="container-fluid status">
            <div class="row">
              <div class="col-lg col-6">
                <div class="weather ">
                  <p class="heading">Tomorrow</p>
                  <img class="images" :src="dayico[0]" />
                  <p class="space">
                    {{ otherdaysmin[0] }}<span v-if="iscelsius">°C</span
                    ><span v-else>°F</span>
                    <span class="col1">
                      {{ otherdaysmax[0] }}<span v-if="iscelsius">°C</span
                      ><span v-else>°F</span>
                    </span>
                  </p>
                </div>
              </div>

              <div class="col-lg col-6">
                <div class="weather ">
                  <p class="heading">{{ daydt[0] }}</p>
                  <img class="images" :src="dayico[1]" />
                  <p class="space">
                    {{ otherdaysmin[1] }}<span v-if="iscelsius">°C</span
                    ><span v-else>°F</span>
                    <span class="col1">
                      {{ otherdaysmax[1] }}<span v-if="iscelsius">°C</span
                      ><span v-else>°F</span>
                    </span>
                  </p>
                </div>
              </div>

              <div class="col-lg col-6">
                <div class="weather ">
                  <p class="heading">{{ daydt[1] }}</p>
                  <img class="images" :src="dayico[2]" />
                  <p class="space">
                    {{ otherdaysmin[2] }}<span v-if="iscelsius">°C</span
                    ><span v-else>°F</span>
                    <span class="col1">
                      {{ otherdaysmax[2] }}<span v-if="iscelsius">°C</span
                      ><span v-else>°F</span>
                    </span>
                  </p>
                </div>
              </div>

              <div class="col-lg col-6">
                <div class="weather ">
                  <p class="heading">{{ daydt[2] }}</p>
                  <img class="images" :src="dayico[3]" />
                  <p class="space">
                    {{ otherdaysmin[3] }}<span v-if="iscelsius">°C</span
                    ><span v-else>°F</span>
                    <span class="col1">
                      {{ otherdaysmax[3] }}<span v-if="iscelsius">°C</span
                      ><span v-else>°F</span>
                    </span>
                  </p>
                </div>
              </div>

              <div class=" col-lg col-sm-6">
                <div class="weather ">
                  <p class="heading">{{ daydt[3] }}</p>
                  <img class="images" :src="dayico[4]" />
                  <p class="space">
                    {{ otherdaysmin[4] }}<span v-if="iscelsius">°C</span
                    ><span v-else>°F</span>
                    <span class="col1">
                      {{ otherdaysmax[4] }}<span v-if="iscelsius">°C</span
                      ><span v-else>°F</span></span
                    >
                  </p>
                </div>
              </div>
            </div>
          </div>
          <br />
          <br />
          <br />

          <h1>Today’s HighLights</h1>
          <div class="container">
            <div class="row">
              <div class=" col condition">
                <p>Wind Status</p>
                <h4>{{ weather.windspeed }} <span class="mph">mph</span></h4>
                <p class="direction">
                  <i class="fas fa-circle wsw"></i>
                  <i
                    class="fas arrow fa-location-arrow"
                    :style="{
                      transform: 'rotate(' + (weather.winddeg - 60) + 'deg)',
                    }"
                  ></i>
                  {{ weather.degstatus }}
                </p>
              </div>

              <div class=" col condition">
                <p>Humidity</p>
                <h4>{{ weather.humidity }}<span class="mph">%</span></h4>
                <!-- <span class="numbers">0 50 100</span> -->
                <span style="float:left">0</span>
                <span>50</span>
                <span style="float:right">100</span>
                <div class="progress">
                  <div
                    class="progress-bar bg-warning"
                    role="progressbar"
                    :style="{ width: weather.humidity + '%' }"
                    aria-valuenow="weather.humidity"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
                <span class="percent"> %</span>
              </div>
            </div>
            <div class="row">
              <div class=" col condition">
                <p>Visibility</p>
                <h4>
                  {{ weather.visibility }},{{ weather.visi1 }}
                  <span class="mph">miles</span>
                </h4>
              </div>

              <div class=" col condition">
                <p>Air pressure</p>
                <h4>{{ weather.pressure }}<span class="mph">mb</span></h4>
              </div>
            </div>
          </div>

          <footer>RazorYan @ DevChallenges.io</footer>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { citymixin } from "./citymixin";
import VueSuggest from "vue-simple-suggest";
import "vue-simple-suggest/dist/styles.css";
import cloud from "./photo/Cloud-background.png";

export default {
  mixins: [citymixin],
  components: {
    VueSuggest,
  },
  data: function() {
    return {
      locate: null,
      iscelsius: true,
      clear: "",
      cloud: cloud,
      search: true,
      query: "mandalay",
      lat: null,
      longit: null,
      api_key: "21e5a096e10ff94852161495ea60ac26",
      weather: {
        temp: "",
        city: "",
        description: "ok",
        windspeed: 0,
        winddeg: 0,
        degstatus: "",
        humidity: 0,
        pressure: 0,
        visibility: 0,
        visi1: 0,
        unixtime: 0,
        weekday: "",
        day: 0,
        month: "",
        // AIzaSyAGQfsAy0g4cxnAAYXbGxs9B29JCfstQCY
      },
      otherdaysmin: [],
      otherdaysmax: [],
      dayico: [],
      daydt: [],
    };
  },
  created() {
    this.fetchData();
  },

  methods: {
    fetchData() {
      this.iscelsius = true;
      fetch(
        "https://api.openweathermap.org/data/2.5/weather?q=" +
          this.query +
          "&appid=" +
          this.api_key
      )
        .then((res) => {
          return res.json();
        })
        .then(this.setResults);

      fetch(
        "https://api.openweathermap.org/data/2.5/forecast?q=" +
          this.query +
          "&appid=" +
          this.api_key
      )
        .then((res) => {
          return res.json();
        })
        .then(this.otherdayResults);
      this.search = true;
    },
    getlocate() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          this.showPosition,
          this.showError
        );
      } else {
        alert("Geolocation is not supported by this browser.");
      }
    },

    showPosition(position) {
      this.iscelsius = true;
      this.locate = true;
      this.lat = position.coords.latitude;
      this.longit = position.coords.longitude;
    
      fetch(
        "https://api.openweathermap.org/data/2.5/weather?lat=" +
          this.lat +
          "&lon=" +
          this.longit +
          "&appid=" +
          this.api_key
      )
        .then((res) => {
          return res.json();
        })
        .then(this.setResults);

      fetch(
        "https://api.openweathermap.org/data/2.5/forecast?lat=" +
          this.lat +
          "&lon=" +
          this.longit +
          "&appid=" +
          this.api_key
      )
        .then((res) => {
          return res.json();
        })
        .then(this.otherdayResults);
    },
    showError() {
      this.locate = false;
    },
    setResults(results) {
     
      this.weather.city = results.name;
      var code = results.weather[0].icon;
      this.clear = require("./photo/" + code + ".png");
      this.weather.temp = Math.floor(results.main.temp - 273);
      this.weather.description = results.weather[0].description;
      this.weather.windspeed = results.wind.speed;
      this.weather.winddeg = results.wind.deg;
      this.weather.humidity = results.main.humidity;
      this.weather.pressure = results.main.pressure;
      this.weather.visibility = Math.round(results.visibility / 1609);
      this.weather.visi1 = Math.round(
        10 * (results.visibility / 1609 - this.weather.visibility)
      );
     
      this.weather.unixtime = results.dt;
      const dateObject = new Date(this.weather.unixtime * 1000);
      this.weather.weekday = dateObject.toLocaleString("en-US", {
        weekday: "short",
      }); // Monday
      this.weather.month = dateObject.toLocaleString("en-US", {
        month: "short",
      }); // December
      this.weather.day = dateObject.toLocaleString("en-US", { day: "numeric" }); // 9
    },
    otherdayResults(results) {
     
      this.otherdaysmin = [];
      this.otherdaysmax = [];
      this.dayico = [];
      this.daydt = [];
      var i;
      for (i = 7; i < 40; i += 8) {
        this.otherdaysmin.push(
          Math.floor(results.list[i].main.feels_like - 273)
        );
        this.otherdaysmax.push(Math.floor(results.list[i].main.temp_max - 273));
        this.dayico.push(
          require("./photo/" + results.list[i].weather[0].icon + ".png")
        );
      }
   

      for (i = 15; i < 40; i += 8) {
        const dateObject = new Date(results.list[i].dt * 1000);
        this.daydt.push(
          "" +
            dateObject.toLocaleString("en-US", { weekday: "short" }) +
            ", " +
            dateObject.toLocaleString("en-US", { day: "numeric" }) +
            " " +
            dateObject.toLocaleString("en-US", { month: "short" }) +
            ""
        );
      }
      this.windDirection();
    },
    windDirection() {
      if (this.weather.winddeg > 348 || this.weather.winddeg < 11) {
        this.weather.degstatus = "N";
      } else if (this.weather.winddeg > 326) {
        this.weather.degstatus = "NNW";
      } else if (this.weather.winddeg > 303) {
        this.weather.degstatus = "NW";
      } else if (this.weather.winddeg > 281) {
        this.weather.degstatus = "WNW";
      } else if (this.weather.winddeg > 258) {
        this.weather.degstatus = "W";
      } else if (this.weather.winddeg > 236) {
        this.weather.degstatus = "WSW";
      } else if (this.weather.winddeg > 213) {
        this.weather.degstatus = "SW";
      } else if (this.weather.winddeg > 191) {
        this.weather.degstatus = "SSW";
      } else if (this.weather.winddeg > 168) {
        this.weather.degstatus = "S";
      } else if (this.weather.winddeg > 146) {
        this.weather.degstatus = "SSE";
      } else if (this.weather.winddeg > 123) {
        this.weather.degstatus = "SE";
      } else if (this.weather.winddeg > 101) {
        this.weather.degstatus = "ESE";
      } else if (this.weather.winddeg > 78) {
        this.weather.degstatus = "E";
      } else if (this.weather.winddeg > 56) {
        this.weather.degstatus = "ENE";
      } else if (this.weather.winddeg > 33) {
        this.weather.degstatus = "NE";
      } else if (this.weather.winddeg > 11) {
        this.weather.degstatus = "NNE";
      }
    },
    Fahrenheit() {
      if (this.iscelsius) {
        this.iscelsius = false;
        this.weather.temp = Math.round(this.weather.temp * 1.8 + 32);
        var i;
        for (i = 0; i < 5; i++) {
          this.otherdaysmin[i] = Math.round(this.otherdaysmin[i] * 1.8 + 32);
          this.otherdaysmax[i] = Math.round(this.otherdaysmax[i] * 1.8 + 32);
        }
      }
    },
    Celsius() {
      if (!this.iscelsius) {
        this.iscelsius = true;
        this.weather.temp = Math.round((this.weather.temp - 32) * 0.56);
        var i;
        for (i = 0; i < 5; i++) {
          this.otherdaysmin[i] = Math.round((this.otherdaysmin[i] - 32) * 0.56);
          this.otherdaysmax[i] = Math.round((this.otherdaysmax[i] - 32) * 0.56);
        }
      }
    },
  },
};
</script>

